<script>
	// import ColorResult from "./ColorResult.svelte"
	let hue = 270
	let saturation = 50
	let lightness = 40
	let r = 102
	let g = 0
	let b = 217
	let pantone = {
		name: 'unknown',
		hsl: 'unknown',
		hex: 'unknown'
	}
	nearestPantone()
	
	function nearestPantone() {
		let nearestArr = Pantone.rgb.map(color => color
														 .reduce((err, val, i) => err += Math.abs(val - [r, g, b][i]), 0))
		let nearest = nearestArr.reduce((min, val, i) => {
			return (min.val < val) ? min : {val, i}}, {val: nearestArr[0], index: 0})
		
														 
		pantone.name = Pantone.names[nearest.i]
		pantone.hsl = Pantone.hsl[nearest.i]
		pantone.hex = Pantone.rgb[nearest.i]
	}
	
	function updateSaturationLightness(e) {
		nearestPantone()
		let {width, height} = e.target.getBoundingClientRect()
		saturation = e.offsetX / width * 100
		lightness = 100 - e.offsetY / height * 100
	}
	
	function convertHSL({hue, saturation, lightness}, toHex) {
		saturation /= 100
		lightness /= 100
		
		let chroma = (1 - Math.abs(2 * lightness - 1) * saturation),
		    x = chroma * (1 - Math.abs((hue / 60) % 2 - 1)),
				m = lightness - chroma / 2;

		if (0 <= hue && hue < 60) {
			r = chroma; g = x; b = 0;
		} else if (60 <= hue && hue < 120) {
			r = x; g = chroma; b = 0;
		} else if (120 <= hue && hue < 180) {
			r = 0; g = chroma; b = x;
		} else if (180 <= hue && hue < 240) {
			r = 0; g = x; b = chroma;
		} else if (240 <= hue && hue < 300) {
			r = x; g = 0; b = chroma;
		} else if (300 <= hue && hue < 360) {
			r = chroma; g = 0; b = x;
		}
		
		r = Math.max(0, Math.round((r + m) * 255));
		g = Math.max(0, Math.round((g + m) * 255));
		b = Math.max(0, Math.round((b + m) * 255));
		
		if (toHex) {
			let red = r.toString(16);
			let green = g.toString(16);
			let blue = b.toString(16);

			if (red.length == 1)
				red = "0" + red;
			if (green.length == 1)
				green = "0" + green;
			if (blue.length == 1)
				blue = "0" + blue;
			
			return `#${red}${green}${blue}`
		} else {
			return `rgb(${r}, ${g}, ${b})`
		}
	}
	
	function copyText(e) {
		let text = e.target.innerText
		let textArea = document.querySelector('.hidden-textarea')
		textArea.setAttribute('value', text)
		textArea.innerText = text
		textArea.select()
		document.execCommand('copy')
		e.target.focus()
	}
</script>

<div id="colorpicker" 
		 style="--hue: {hue}deg;
						--saturation: {saturation}%;
						--lightness: {lightness}%;
						--contrast: {(lightness > 50) ? '#222' : 'white'};">
	<div id="result" >
		<div class="result-item">
			<h3>HSL</h3>
			<div tabindex=0 on:click={copyText}>
				hsl({hue}deg, {saturation.toFixed(1)}%, {lightness.toFixed(1)}%)
			</div>
		</div>
		<div class="result-item">
			<h3>RGB</h3>
			<div tabindex=0 on:click={copyText}>
				{convertHSL({hue, saturation, lightness}, false)}
			</div>
		</div>
		<div class="result-item">
			<h3>Hex</h3>
			<div tabindex=0 on:click={copyText}>
				{convertHSL({hue, saturation, lightness}, true)}
			</div>
		</div>
		<div class="result-item">
			<h3>Nearest Pantone</h3>
			<div tabindex=0 on:click={copyText}>
				{pantone.name}:<br> {pantone.hsl}
			</div>
		</div>
	</div>
	<div id="sl-picker" on:click={updateSaturationLightness}>
		<div id="sl-dot" style="--dot-x: {saturation}%;
														--dot-y: {lightness}%;">
		</div>
	</div>
	<label id="h-picker">
		<input type="range" min=0 max=360 bind:value={hue} on:input={nearestPantone}>
		<div id="h-dot" style="--dot-x: {hue / 360 * 80 + 10}%;"></div>
	</label>
	<textarea class="hidden-textarea"></textarea>
</div>

<style>
